.explore-projects {
	opacity: 0;
	padding: 0 0 80px 0;
	transition: opacity 0.5s ease-out;
	position: relative;
}
.explore-projects.loaded {
	opacity: 1;
}
.error h5 {
	margin-top: 60px;
}
.explore-container {
	position: relative;
}
canvas {
	display: block;
	margin: 0 auto;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
.selected-project {
	-webkit-animation: none;
	animation: none;
	background: hsla(0, 0%, 100%, 0);
	border-radius: 50%;
	height: 340px;
	left: 50%;
	margin: -170px 0 0 -170px;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	text-align: center;
	top: 50%;
	width: 340px;
}
.selected-project a {
	opacity: 0;
}
.selected-project.active,
.selected-project.active a {
	-webkit-animation: fade-in 0.4s ease-in-out forwards;
	animation: fade-in 0.4s ease-in-out forwards;
	cursor: pointer;
}
.selected-project .project-summary {
	height: 260px;
}
.selected-project .project-summary img {
	width: 60px;
	height: 60px;
}
.selected-project a,
.selected-project h5,
.selected-project p {
	margin: 0 15px 15px 15px;
}
.selected-project .icon {
	max-height: 60px;
}
.selected-project img {
	margin: 30px auto 0 auto;
}
.selected-project p {
	font-size: 14px;
	line-height: 18px;
	max-height: 145px;
	overflow: hidden;
}
.page {
	-webkit-animation: fade-in 2s ease-in-out 1s forwards;
	animation: fade-in 2s ease-in-out 1s forwards;
	// background:url(/assets/images/chevron-dark.svg) 0 0 no-repeat;
	cursor: pointer;
	height: 15px;
	left: 10px;
	margin: -15px 0 0 0;
	opacity: 0;
	overflow: hidden;
	position: absolute;
	text-indent: -9999px;
	top: 50%;
	-webkit-transform: rotate(90deg);
	transform: rotate(90deg);
	-webkit-transform-origin: center;
	transform-origin: center;
	transition: -webkit-transform 0.1s ease-in;
	transition: transform 0.1s ease-in;
	transition: transform 0.1s ease-in, -webkit-transform 0.1s ease-in;
	width: 20px;
}
.page.next {
	left: auto;
	right: 10px;
	-webkit-transform: rotate(-90deg);
	transform: rotate(-90deg);
}
.page:hover {
	-webkit-transform: rotate(90deg) scale(1.4);
	transform: rotate(90deg) scale(1.4);
}
.page.next:hover {
	-webkit-transform: rotate(-90deg) scale(1.4);
	transform: rotate(-90deg) scale(1.4);
}
@-webkit-keyframes fade-in {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
@keyframes fade-in {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
